.gallerywow {
  background: #f1f1f1;
  padding: 15px;
}

.wrap-media-gallery {
  padding: 15px 0;
  border-bottom: 1px solid lightblue;
}

.gallerywow ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 33.3% 33.3% 1fr;
  justify-content: flex-start;
  width: 100%;
}